import React from 'react';
import "./mystyles.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVideo, faImages} from '@fortawesome/free-solid-svg-icons'
import Layout from "../components/layout"

const Articuloa = () => {
    return (
        <Layout>
            <div className="articuloA contenedor"> 
            <div className="a_section" >
                <div className="a_section_textContent">
                    <h1 className="titulo">Titulo</h1>
                    <h1 className="subTitulo">Subtitulo</h1>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Explicabo recusandae consectetur magnam! Laborum nihil culpa aut adipisci illo nobis mollitia nulla saepe, modi ipsum molestiae sequi accusantium labore deleniti autem.</p>
                </div>
                <div className="articleButtons level" >
                    <div className="level-item has-text-centered"> 
                        <FontAwesomeIcon size="3x" className="articuloAIcons" icon={faImages} />
                    </div>
                    <div className="level-item has-text-centered"> 
                        <FontAwesomeIcon size="3x" className="articuloAIcons" icon={faVideo} />
                    </div>
                </div>
            </div>

            <div className="b_section" >

            </div>
            
        </div>
        </Layout>
    );
}

export default Articuloa;
